	<div class="contentWrap">
		<div class="content">
			<div class="leftContent">
				<div class="leftWrap">
					<div class="title">
						<!-- <img src="../assets/img/logo.png" alt="" /> -->
						<img src="../assets/img/brand.png" alt="" />
					</div>
					<div class="tyreBrand">
