			for (let i = 0; i < 7; i++) {
				for (let j = 0; j < 6; j++) {
					if (newData[i] && JSON.stringify(newData[i]) != "{}") {
						if (newData[i].data[j]) {
							if (elementId == "tyreTemperatureInfo4") {
								dataValue.push([
									i,
									j,
									newData[i].data[j + 1].tyreTemperature_mean || 0,
