							} else if (elementId == "tyrePressureInfo4") {
								dataValue.push([
									i,
									j,
									newData[i].data[j + 1].tyrePressure_mean || 0,
								]);
							}
						} else {
							dataValue.push([i, j, 0]);
						}
					} else {
