		async selectedTime(index, type) {
			let _this = this;
			let elementID;
			let newHistoryData;
			let newTyreArr = [];
			if (index == "1") {
				newHistoryData = await _this.getDataByHours();
			} else {
				newHistoryData = await _this.getDataByTime(index);
			}
			if (type == "tyreTemperature") {
				_this.isSelected = index;
				elementID = "tyreTemperatureInfo1";
				if (index != "1") {
					newHistoryData.tyreTemperature.map((item) => {
						newTyreArr.push(JSON.parse(item));
					});
				} else {
					newTyreArr = newHistoryData.tyreTemperature;
				}
			} else if (type == "tyrePressure") {
				_this.isSelected1 = index;
				elementID = "tyrePressureInfo1";
				if (index != "1") {
					newHistoryData.tyrePressure.map((item) => {
						newTyreArr.push(JSON.parse(item));
					});
				} else {
					newTyreArr = newHistoryData.tyrePressure;
				}
			}
			// console.log("newTyreArr", newTyreArr);
			switch (index) {
				case "1":
					_this.xAxisName = "分段";
					_this.getTyreChart1(elementID, _this.minutesArr, newTyreArr, index);
					break;
				case "2":
					_this.xAxisName = "时段";
					_this.getTyreChart1(
						elementID,
						_this.hoursArr,
						newTyreArr[0].data,
						index
					);
					break;
				case "3":
					_this.xAxisName = "天";
					_this.getTyreChart1(elementID, _this.threeDaysArr, newTyreArr, index);
					break;
				case "4":
					_this.xAxisName = "天";
					_this.getTyreChart1(elementID, _this.daysArr, newTyreArr, index);
					break;
			}
		},
		async getDataByHours() {
			let params = {
				tyreID: this.tyreID,
			};
			return await this.$http
				.post(this.$serverAPI["根据时间获取胎温胎压"], params)
				.then(
					(res) => {
						if (res.state == 0) {
							return res.result;
						}
					},
					(e) => {
						console.log(e);
					}
				);
		},
		async getDataByTime(time) {
			let params = {
				tyreID: this.tyreID,
				day: time == "2" ? "1" : time == "3" ? "3" : time == "4" ? "7" : "",
			};
			return await this.$http
				.post(this.$serverAPI["根据天数获取胎温胎压"], params)
				.then(
					(res) => {
						if (res.state == 0) {
							this.newHistoryData = res.result;
							return res.result;
						}
					},
					(e) => {
						console.log(e);
						// this.$message.error('获取信息失败')
					}
				);
		},
	},
};
